.App{
  text-align: center;
}

.black-nav{
  background: #000;
  padding: 20px;
  box-sizing: border-box;
  color: #fff;
  display: flex;
  justify-content: flex-start;
}
div{box-sizing: border-box;}
.list{
  text-align: left;
  padding-left: 20px;
  border-bottom: 1px solid grey;
}
.list>h4:nth-child(1){position: relative; padding-left: 10px;}
.list>h4:nth-child(1):after{position: absolute; content:""; width: 3px; height:15px; background: #fcff4a; top: 3px; left: 0px;}
button{border: none; background: #eeb5ff; color: whitesmoke; margin: 10px; padding: 20px 40px;}
.modal{margin: 20px; padding: 20px; background: #bbf1ff; box-sizing: border-box;}
span{padding: 0 10px;}
input{padding: 10px; width: 300px;} 